  width: 35px;
  height: 35px;
  color: var(--dark);
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  transition: color .25s linear, transform .25s linear;
  position: absolute;
  top: 4px;
  right: 0;
}

