  justify-content: center;
  align-items: center;
  transition: visibility .25s, background-color .25s, backdrop-filter .25s;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.popcorn-loader.visible {
  visibility: visible;
  backdrop-filter: blur(4px);
  background-color: rgba(226, 224, 224, .1);
}

.popcorn-loader__gif {
  width: 150px;
  height: 150px;
  visibility: hidden;
  opacity: 0;
