@charset 'utf-8';

.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  position: absolute;
  top: -240%;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 28px;
  line-height: normal;
  text-align: center;
  font-size: 0;

  @include mq(tab) {
    top: -410%;
  }
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 28px;
  padding: 8px 0 6px;
  margin-left: 5px;
  color: #333;
  font-size: 12px;
  font-weight: normal;
  line-height: 1;
  text-decoration: none;
  vertical-align: middle;
  border-width: 1px 0;
  transition: all 0.2s ease-in-out;
  border-radius: 4px;
}

.tui-pagination .tui-page-btn:hover {
  transform: scale(1.2);
  background-color: #f4f4f4;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  width: 40px;
  height: 40px;
  padding: 14px 0 6px;
  color: #fff;
  background: var(--accent);
  border-color: var(--accent);
  border-radius: 4px;
  cursor: default;
  transition: all 0.2s ease-in-out;
}

.tui-pagination .tui-is-selected:hover {
  background-color: var(--accent);
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first-child,
.tui-pagination .tui-prev-is-ellip {
  border-left: 1px solid #ddd;
  border: 0;
}

.tui-pagination .tui-last-child,
.tui-pagination .tui-next-is-ellip {
  border-right: 1px solid #ddd;
  transition: all 0.2s ease-in-out;
}

.tui-pagination .tui-first-child.tui-is-selected {
  border-left: 1px solid var(--accent);
}

.tui-pagination .tui-last-child.tui-is-selected {
  border-right: 1px solid var(--accent);
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  height: 26px;
  padding: 0;
  font-size: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  border: 1px solid #ddd;
  border: 0;
}

.tui-pagination .tui-prev-is-ellip {
  border-right: 0;
}

.tui-pagination .tui-next-is-ellip {
  border-left: 0;
}

.tui-pagination .tui-first + .tui-prev,
.tui-pagination .tui-next + .tui-last {
  border-left: 0;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev,
.tui-pagination .tui-next {
  width: 40px;
  height: 40px;
  padding-top: 7px;
  vertical-align: middle;
  background-color: var(--pagination-nex-prev);
  border-radius: 4px;
  transition: background-color 0.2s ease-in-out;

  &:hover,
  &:focus {
    background-color: #ebe9e9;
  }
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

.tui-pagination .tui-prev {
  margin-right: 10px;
  transition: all 0.2s ease-in-out;
}

.tui-pagination .tui-next {
  margin-left: 10px;
  transition: all 0.2s ease-in-out;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: default;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #b92f2c;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

.tui-page-btn.tui-first,
.tui-page-btn.tui-last {
  display: none;
}
